"use client";

import { Button, Modal } from "@heroui/react";

interface ConfirmModalProps {
    isOpen: boolean;
    title?: string;
    message: string;
    userName?: string;
    onConfirm: () => void;
    onCancel: () => void;
    isLoading?: boolean;
    confirmText?: string;
    cancelText?: string;
    isDanger?: boolean;
}

export function ConfirmModal({
    isOpen,
    title = "Confirm Action",
    message,
    userName,
    onConfirm,
    onCancel,
    isLoading = false,
    confirmText = "Yes, Confirm",
    cancelText = "Cancel",
    isDanger = false,
}: ConfirmModalProps) {
    return (
        <Modal isOpen={isOpen} onOpenChange={onCancel}>
            <Modal.Header className="flex flex-col gap-1 text-white bg-zinc-900 border-b border-zinc-800">
                <div className="flex items-center gap-2">
                    {isDanger && (
                        <div className="w-8 h-8 rounded-full bg-red-900/30 flex items-center justify-center">
                            <svg className="w-5 h-5 text-red-400" fill="currentColor" viewBox="0 0 20 20">
                                <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                            </svg>
                        </div>
                    )}
                    <h2>{title}</h2>
                </div>
            </Modal.Header>
            <Modal.Body>
                <p className="text-gray-300">
                    {message}
                    {userName && <strong className="text-white"> "{userName}"</strong>}
                    ?
                </p>
            </Modal.Body>
            <Modal.Footer className="gap-2">
                <Button
                    variant="ghost"
                    onPress={onCancel}
                    isDisabled={isLoading}
                    className="text-gray-400 hover:text-white"
                >
                    {cancelText}
                </Button>
                <Button
                    onPress={onConfirm}
                    isDisabled={isLoading}
                    className={isDanger ? "bg-red-600 hover:bg-red-700 text-white" : "bg-blue-600 hover:bg-blue-700 text-white"}
                >
                    {isLoading ? "Loading..." : confirmText}
                </Button>
            </Modal.Footer>
        </Modal>
    );
}