import { useUserStore } from '@/stores/user/userStore';
import NextLink from 'next/link';
import { Button } from '@heroui/react';

export default function UserFilter() {
    const { searchQuery,
        roleFilter,
        statusFilter,
        setSearchQuery,
        setRoleFilter,
        setStatusFilter,
        deleteUser,
        getFilteredUsers } = useUserStore();

    const filteredUser = getFilteredUsers();

    const getRoleBadgeColor = (role: string) => {
        switch (role) {
            case 'Administrator': return 'bg-purple-600/20 text-purple-400 border-purple-600/30';
            case 'Field Supervisor': return 'bg-orange-600/20 text-orange-400 border-orange-600/30';
            case 'Engineer': return 'bg-blue-600/20 text-blue-400 border-blue-600/30';
            default: return 'bg-gray-600/20 text-gray-400 border-gray-600/30';
        }
    };

    return (
        <>
            <div className="flex gap-4 mb-6">
                {/* Search */}
                <div className="flex-1">
                    <div className="relative">
                        <svg className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                        </svg>
                        <input
                            type="text"
                            placeholder="Search by name or email..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="w-full bg-zinc-900 border border-zinc-800 rounded-lg pl-10 pr-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700"
                        />
                    </div>
                </div>

                {/* Role Filter */}
                <select
                    value={roleFilter}
                    onChange={(e) => setRoleFilter(e.target.value)}
                    className="bg-zinc-900 border border-zinc-800 rounded-lg px-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700 min-w-[150px]"
                >
                    <option>All Roles</option>
                    <option>Administrator</option>
                    <option>Field Supervisor</option>
                    <option>Engineer</option>
                </select>

                {/* Status Filter */}
                <select
                    value={statusFilter}
                    onChange={(e) => setStatusFilter(e.target.value)}
                    className="bg-zinc-900 border border-zinc-800 rounded-lg px-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700 min-w-[150px]"
                >
                    <option>All Status</option>
                    <option>Active</option>
                    <option>Inactive</option>
                </select>

                {/* Create User Button */}
                <NextLink href="/user/new">
                    <Button className="bg-red-600 hover:bg-red-700 text-white px-6">
                        Create User
                    </Button>
                </NextLink>
            </div>
        </>
    )
}
